  line-height: var(--body-line-height);
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }
  body::before {
    display: none; }
  body::before {
    content: "xs"; }
  @media (min-width: 576px) {
    body::before {
      content: "sm"; } }
  @media (min-width: 768px) {
    body::before {
      content: "md"; } }
  @media (min-width: 992px) {
    body::before {
      content: "lg"; } }
  @media (min-width: 1200px) {
    body::before {
      content: "xl"; } }

