/*╔══════════════════════════════════════════════════╗
  ║                        Definition                         ║
  ╚══════════════════════════════════════════════════╝*/
dl dd,
dl dt {
  position: relative; }
  dl dd::before, dl dd::after,
  dl dt::before,
  dl dt::after {
    position: absolute;
    border-bottom: 2px dotted var(--shade-2);
    content: "";
    top: 50%;
    bottom: auto;
    transform: translateY(-50%); }

dl dt::before {
  left: -17px;
